
.online {
  padding-top: 40px;
}
.divider {
  width: 100%;
  height: 1px;
  background-color: #e9e9e9;
}
.table_header {
  width: 100%;
  height: 28px;
  margin-top: 20px;
  text-align: center;
  line-height: 28px;
  color: #999;
  font-family: '微软雅黑';
  font-weight: 400;
  font-style: normal;
  font-size: 12px;
}
.table_header div {
  float: left;
}
.cell1 {
  width: 30%;
  height: 100%;
  border-right: 1px solid #e9e9e9;
}
.cell2 {
  width: 39%;
  height: 100%;
  border-right: 1px solid #e9e9e9;
}
.cell3 {
  width: 29%;
  height: 100%;
}
.table_row {
  width: 100%;
  height: 46px;
  font-size: 14px;
  color: #333;
  line-height: 46px;
  font-weight: 600;
}
.table_row div {
  float: left;
}
.table_row span {
  display: block;
  width: auto;
  height: 100%;
}
.cell1 span {
  margin-left: 14px;
}
.cell2 span {
  text-align: right;
  margin-right: 14px;
}
.cell3 span {
  text-align: right;
  margin-right: 14px;
}
.odd_row {
  background-color: #fafafa;
}
.shape_item .bar_left {
  float: left;
  margin-left: 3%;
  width: 40%;
  height: 36px;
  font-size: 1.2rem;
  text-align: left;
  line-height: 36px;
  color: #333;
}
.shape_item .bar_right {
  float: right;
  margin-right: 3%;
  width: 40%;
  height: 36px;
  font-size: 1.2rem;
  text-align: right;
  line-height: 36px;
  color: #999;
}
.shape_item .bar {
  float: left;
  width: 94%;
  margin-left: 3%;
  height: 16px;
  position: relative;
}
.shape_item .progress {
  position: absolute;
  top: 0px;
  width: 10%;
  height: 3px;
  background-color: #14b9ef;
  border: 1px solid #14b9ef;
}
.shape_item .progress_bg {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 3px;
  background-color: #f4f4f4;
  border: 1px solid #f4f4f4;
}
.shape_item {
  width: 100%;
  height: 62px;
  position: relative;
  margin-top: 10px;
}


