
.state {
  padding-top: 40px;
}
.state .content section {
  margin: 16px 12px;
  letter-spacing: 2px;
  line-height: 1.2rem;
  text-align: justify;
}
.state .content .subtitle {
  padding: 8px;
  border-bottom: 1px solid #e9e9e9;
  text-align: center;
  color: #48cfad;
  font-size: 1.1rem;
}
.state .content .sbstract {
  border: 1px dashed #ccc;
  padding: 8px;
}
.state .content .sbstract span {
  font-weight: bold;
}
.state .content .box {
  padding: 10px;
  border: 1px solid rgba(0,0,0,0.15);
  border-top: 5px solid #48cfad;
  box-shadow: 0 3px 6px rgba(0,0,0,0.175);
}
.state .content .box h3 {
  padding: 12px;
  color: #48cfad;
  font-size: 1.2rem;
  font-weight: bold;
}
.state .content .box .sect {
  margin-top: 10px;
}
.state .content .box .sect .state-a {
  color: #26a2ff;
}
.state .content .box .describe {
  margin-top: 18px;
  width: 100%;
  height: 100%;
}


